* {
  -webkit-user-select: none;
  user-select: none;
}

.home {
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.logo-div {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 70vh;
  margin-top: 2rem;
  display: flex;
  position: relative;
}

.logo-div p {
  text-align: center;
  margin: 2rem 0 0;
  font-size: 1.4rem;
}

.logo-div .venue-text {
  margin: 0 0 2rem;
}

.home #title {
  border-right: .15em solid var(--primary-blue);
  min-height: 39px;
  margin: 2rem 0;
  font-size: 1.6rem;
  font-weight: 600;
  animation: .75s step-end infinite blink-caret;
}

@keyframes blink-caret {
  from, to {
    border-color: #0000;
  }

  10% {
    border-color: var(--primary-blue);
  }
}

.sec-text {
  font-size: 2rem;
}

.logo-home {
  width: 35%;
}

.logo-home img {
  object-fit: contain;
  width: 100%;
  height: 100%;
  animation: .6s cubic-bezier(.3, .8, 1, 1.05) 6.2s both animeAfterLoad;
}

@keyframes animeAfterLoad {
  0% {
    opacity: 0;
    transform: scale(5)skew(0deg);
  }
}

.butHolder {
  flex-flow: wrap;
  justify-content: space-around;
  align-items: center;
  gap: 1rem;
  min-width: 40%;
  margin-top: 1rem;
  display: flex;
}

.Btn {
  cursor: pointer;
  border-radius: .25rem;
  justify-content: center;
  align-items: center;
  width: 312px;
  height: 44px;
  font-size: 1.2rem;
  display: flex;
  position: relative;
  overflow: hidden;
  transform: scale(1);
  box-shadow: 0 0 #0000, 0 0 #0000, 0 1px 3px #0000001a, 0 1px 2px -1px #0000001a;
  border: none !important;
  outline: none !important;
}

.Btn .icon {
  color: var(--white);
  font-size: 1.6rem;
  position: absolute;
  top: 50%;
  left: 8%;
  transform: translate(-6%, -50%);
}

.Btn .text {
  z-index: 1;
  color: var(--primary-blue);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-25%, -50%);
}

.Btn:active {
  transform: scale(.95);
}

.gradient-mask {
  width: 25%;
  height: 100%;
  transition-property: all;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(0, 0, .2, 1);
  position: absolute;
  inset: 0;
}

.Btn:hover .gradient-mask {
  width: 100%;
}

.Btn:hover .text {
  color: var(--white);
}

.black-bg {
  background-image: linear-gradient(to right, #434343, #000);
}

.zinc-bg {
  background-image: linear-gradient(to left, #27272a, #27272b);
}

.blue-bg {
  background-image: linear-gradient(to left, #1e40af, #60a5fa);
}

.cost1.build-in-animate {
  animation-play-state: running;
}

.cost1 {
  animation: 4s infinite floaty;
  position: absolute;
  left: 7%;
}

@keyframes floatx {
  0%, 100% {
    transform: translateX(0);
  }

  50% {
    transform: translateX(-10px);
  }
}

@keyframes floaty {
  0%, 100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-10px);
  }
}

@keyframes constmove {
  0% {
    top: 30%;
  }

  50% {
    top: 50%;
  }

  100% {
    top: 30%;
  }
}

.cost2 {
  width: -moz-fit-content;
  width: fit-content;
  animation: 6s infinite floaty;
  position: absolute;
  top: 40%;
  right: 6%;
}

.cost1 img {
  width: 60%;
}

.cost2 img {
  width: 70%;
}

.TagContainer .tagText {
  position: relative;
}

.tagText.sec-text:before {
  content: "";
  border-left: 2px solid var(--white);
  width: 100%;
  height: 100%;
  animation: 2s steps(6, end) infinite animate;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes animate {
  40%, 60% {
    left: calc(100% + 4px);
  }

  100% {
    left: 0%;
  }
}

@media screen and (max-width: 1000px) {
  .Btn {
    width: 312px;
    height: 44px;
  }
}

@media only screen and (max-width: 768px) {
  .cost2 {
    width: 75px;
    top: 16%;
    right: 0%;
  }

  .cost1 {
    opacity: .7;
    bottom: 17%;
    left: 5%;
  }

  .cost1 img {
    width: 44%;
  }

  .cost2 img {
    width: 82%;
  }

  .Btn {
    width: 312px;
    height: 44px;
  }

  .logo-div {
    height: 50vh;
  }

  .logo-home {
    width: 85%;
  }

  .home #title {
    margin: 2rem 0 .5rem;
  }

  .logo-div p {
    text-align: center;
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 1rem;
  }

  .sec-text {
    font-size: 1.4rem;
  }
}

